.Modal {
    position: relative;
    display: flex;
    margin: 35px;
    justify-content: center;

}

.Modal p{
    color: antiquewhite;
    font-size: 20px;
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
}

@media (min-width: 1000px) {
    .Modal {
        width: 350px;
    }
}